<!-- by 添少 -->
<template>
  <section>
    <mt-header title="上传封面">
      <router-link to="/" slot="left">
        <mt-button icon="back" @click="$router.go(-1)"></mt-button>
      </router-link>
      <mt-button @click="handNext" slot="right">下一步</mt-button>
    </mt-header>
    <section class="upload">
      <template v-if="!cover">
        <h3 class="fw4">上传封面</h3>
      </template>
      <template v-else>
        <h3 class="fw4">上传成功</h3>
      </template>
      <file-image2
        :src="cover"
        @load="handLoadFile"
        @delete="handDeleteImage">
      </file-image2>
      <p>注：该封面图片会出现在“约”的文章列表及文章头部</p>
    </section>
    <template v-if="cropper.dialog">
      <cropper-box :src="cropper.src" @cancel="cropper.dialog = false" @done="handCropperDone"></cropper-box>
    </template>
  </section>
</template>
<script>
  import {mapState} from 'vuex'
  import FileImage2 from '@/components/file-image2'
  import CropperBox from '@/components/cropper-box'

  export default {
    name: '',
    components: {
      FileImage2,
      CropperBox,
    },
    props: {},
    data() {
      return {
        cropper: {
          dialog: false,
          src: '',
        },
        detail: {}
      }
    },
    watch: {},
    computed: {
      ...mapState({
        userType: state => state.userInfo.userType,
        cover: state => state.write.article.cover,
      })
    },
    created() {
      this.init()
    },
    methods: {
      init() {
        if (this.$route.query.productId) {
          this.$http.get(this.$port.ARTICLE_PROT, {
            params: {
              productId: this.$route.query.productId
            }
          }).then(res => {
            if (res.code === 200) {
              this.$store.commit(this.$actionType.set_article_data, {
                contType: this.$route.query.contType,
                productId: this.$route.query.productId,
                cover: res.data.preimg,
                title: res.data.title,
                desc: res.data.desc,
                content: res.data.cont,
              })
            } else {
              alert(res.msg)
            }
          })
        } else {
          this.$store.commit(this.$actionType.set_article_data, {
            productId: 0,
            contType: 1,
            cover: '',
            clubId: -1,
            title: '',
            desc: '',
            content: ''
          })
        }
      },
      initData() {
      },
      handDeleteImage() {
        this.$store.commit(this.$actionType.set_article_data, {
          cover: ''
        })
      },
      handLoadFile(event, file) {
        console.log(event, file)
        this.cropper.dialog = true
        setTimeout(() => {
          this.cropper.src = event.target.result
        }, 100)
      },
      handCropperDone(cropper) {
        let cas = cropper.getCroppedCanvas({
          width: 350,
          height: 350
        })
        cas.toBlob(blob => {
          let fd = new FormData()
          fd.append('imgType', 1)
          fd.append('upload', blob)
          this.$http.post(this.$port.UPLOAD_IMG, fd).then(res => {
            if (res.code === 200) {
              this.$store.commit(this.$actionType.set_article_data, {
                cover: res.data,
                contType: (() => {
                  let contType = parseInt(this.$route.query.contType)
                  if (isNaN(contType)) {
                    contType = 1
                  }
                  return contType
                })()
              })
              this.cropper.dialog = false
            } else {
              alert(res.msg)
            }
          })
        })
      },
      handNext() {
        if (this.cover) {
          alert('请上传图片')
          return
        }
        if (this.userType === 4) {
          this.$router.push({
            path: '/write/club'
          })
        } else {
          this.$router.push({
            path: 'edit',
            query: this.$route.query
          })
        }
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>

  .upload {
    background-color: #fff;
    text-align: center;
    padding-bottom: 10px;
    margin-top: 10px;
    h3 {
      font-size: 24px;
      font-weight: 400;
      line-height: 65px;
      padding: 0;
      margin: 0;
    }
    p {
      font-size: 14px;
      color: #adadad;
    }
    .ui-upload {
      margin-bottom: 10px;
    }
  }

  .ui-upload {
    width: 34.4vw;
    height: 26.4vw;
  }

  .uploadImage {
    width: 34.4vw;
    height: 26.4vw;
    margin-bottom: 10px;
    position: relative;
    border: solid #e5e5e5 1px;
    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: contain;
    }
    b {
      background-color: rgba(0, 0, 0, 0.5);
      text-align: center;
      font-weight: 400;
      font-size: 8px;
      color: #fff;
      width: 20px;
      height: 20px;
      position: absolute;
      right: 0;
      top: 0;
    }
  }
</style>



